.section-1 {
  background-color: #1a1a32;
  color: #fff;
  padding: 60px 0;
  padding-top: 10px;
}

.section-1 p {
  font-size: 1.2rem;
  line-height: 1.5;
}

.section-1 strong {
  font-weight: bold;
  color: #42ab77;
}

.section-1 em {
  font-style: italic;
}

.skills {
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
}

.hard-skills,
.soft-skills {
  flex-basis: 45%;
  max-width: 400px;
  min-height: 350px;
  margin: auto;
  background-color: #266171;
  border-radius: 5px;
  box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.5);
  /* sombra de 2px em x e y, 5px de desfoque, opacidade de 50% */
  color: #fff;
}

.hard-skills h1,
.soft-skills h1 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 20px;
  color: #18fc53;
}

.skills ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.skills li {
  margin-bottom: 10px;
}

.skills li:before {
  content: '';
  color: #fff;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

#soft-skills-icon {
  width: 300px;
  height: 300px;
  fill: #fff;
}

.container {
  max-width: 80%;
  margin: auto;
  text-align: justify;
  padding: 50px;
  padding-top: 0%;
}

.separator {
  height: 1px;
  background-color: #42ab77;
  margin-bottom: 15px;
  margin-top: 15px;
}

.container h1 {
  text-align: center;
  margin-bottom: 50px;
}

.section-title {
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.75);
}

.hard-skills table {
  margin: auto;
}

.hard-skills table {
  border: none;
  border-collapse: collapse;
}

.hard-skills table td,
table th {
  text-align: left;
  padding: 8px;
  border: none;
}

.hard-skills i {
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.75);
  padding-right: 8px;
}
.soft-skills table {
  border: none;
  border-collapse: collapse;
  margin: auto;
}

.soft-skills table td,
table th {
  text-align: left;
  padding: 8px;
  border: none;
}

.soft-skills i {
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.75);
  padding-right: 8px;
}