.project-card {
  width: 100%;
  max-width: 400px;
  height: 640px;
  margin: 0 auto;
  background-color: #1a1a32;
  border-radius: 10px;
  box-shadow: 1px 5px 2px rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
}


.project-card img {
  width: auto;
  height: 200px;
  margin: 5%;
}

.project-card h2 {
  font-size: 28px;
  font-weight: bold;
  color: #18fc53;
  margin: 20px;
  margin-top: 5px;
  margin-bottom: 5px;
  text-align: center;
}

.project-card p {
  font-size: 18px;
  color: #fff;
  margin: 5%;
  margin-bottom: 3%;
  text-align: justify;
  min-height: 130px;
}

.project-card-links {
  display: flex;
  margin-left: 20%;
  margin-right: 21%;
}

.project-card a {
  font-size: 16px;
  font-weight: bold;
  color: #1a1a32;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  margin: 10px auto 0;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  display: block;
  text-align: center;
  width: 20%;
}

.project-card a:hover {
  background-color: #18fc53;
}

.project-card .hashtags {
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
  margin-top: 40px;
  justify-content: center;
}

.project-card .hashtags span {
  font-size: 14px;
  color: #1a1a32;
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 5px 10px;
  background-color: #42ab77;
  border-radius: 20px;
  border: 1px solid #1a1a32;
}

.no-demo {
  opacity: 0.15;
}