.navigation {
  display: flex;
  align-items: center;
}

.navigation a {
  margin-left: 16px;
  color: #fff; 
  text-decoration: none;
  font-weight: bold; 
  transition: color 0.2s ease; 
}

.navigation a:hover {
  color: #18fc53;
}

.navigation-container {
  display: flex;
  background-color: #266171;
  justify-content: center;
  align-items: center;
  border: 1px solid #42ab77;
  height: 40px;
  box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.5);
}

