.carousel-container {
  display: flex;
  flex-direction: column;
  padding-top: 40px;
}

.filters {
  margin: auto;
  width: 78em;
  background-color: #1a1a32;
  margin-bottom: 30px;
  border-radius: 10px;
  box-shadow: 1px 5px 2px rgba(0, 0, 0, 0.7);
  padding-bottom: 5px;
}

#show-all {
  margin: auto;
  margin-bottom: 15px;
}

.filters-header {
  margin: 20px;
  display: flex;
  flex-direction: column;
}

.carousel {
  display: flex;
  justify-content: space-between;
  margin: auto;
}

.carousel img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  margin-right: 0px; /* Espao entre as imagens, ajuste conforme necessrio */
}

.carousel img:last-child {
  margin-right: 0; /* Remove a margem  direita para a ltima imagem */
}

/* Estilo dos botes de navegao */
.carousel-buttons {
  display: flex;
  justify-content: center;
  margin-top: 5px; 
  margin-bottom: 50px; 
}

.carousel-buttons button {
  padding: 12px 20px;
  margin: 0 10px;
  background-color: #fff;
  color: #1a1a32;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.carousel-buttons button:hover {
  background-color: #18fc53;
}

.carousel-buttons button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.carousel-dots {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  min-height: 50px;
  align-items: center;
}

.dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
  margin: 0 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.dot.active {
  width: 12px;
  height: 12px;
  background-color: #18fc53;
}

.custom-checkbox-div {
  display: inline-block;
  position: relative;
  cursor: pointer;
  padding: 5px 10px;
  margin-bottom: 10px;
}

.custom-checkbox-span {
  font-size: 14px;
  color: #1a1a32;
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 5px 10px;
  background-color: #fff;
  border-radius: 20px;
  border: 1px solid #1a1a32;
  cursor: pointer;
}

.custom-checkbox-span-checked {
  font-size: 14px;
  color: #1a1a32;
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 5px 10px;
  background-color: #42ab77;
  border-radius: 20px;
  border: 1px solid #1a1a32;
  cursor: pointer;
}