.language-selector-container {
  display: flex;
  gap: 40px;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  color: white;
}

.language-button {
  background: none;
  border: none;
  cursor: pointer;
  opacity: 0.6; /* Opacidade padro */
  transition: opacity 0.3s ease;
}

.language-button.selected {
  opacity: 1; /* Opacidade quando selecionado */
}

.language-selector-container img {
  width: 32px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.75);
}

.language {
  display: flex;
  flex-direction: column;
  opacity: 0.6; /* Opacidade padro */
}

.language.selected {
  opacity: 1; /* Opacidade padro */
}