.scroll-to-top-arrow {
  position: fixed;
  bottom: 20px;
  left: 20px; /* Alterado para posicionar  esquerda */
  color: white;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Sombra suave */
}

.scroll-to-top-arrow.visible {
  opacity: 1;
}

/* Estilizao adicional para a seta */
.scroll-to-top-arrow span {
  font-size: 55px;
  margin: auto;
}
