.timeline {
  position: relative;
  width: 100%;
}

.timeline-line {
  width: 100%;
  height: 2px;
  background-color: #ccc;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  z-index: 0;
}

.timeline-events {
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 1;
}

.timeline-event {
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #31afdb;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.timeline-event.active {
  background-color: #00bc8c;
}

.year {
  position: absolute;
  bottom: -25px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 12px;
  white-space: nowrap;
}

.description {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  font-size: 14px;
  white-space: nowrap;
}
