.container-footer {
  padding: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-footer span {
  padding-left: 25px;
  padding-top: 0px;
  padding-bottom: 0px;
  padding-right: 25px;
}

.container-footer span i {
  padding-right: 8px;
}

.container-footer a {
  color: #fff; 
  text-decoration: none;
  font-weight: bold; 
  transition: color 0.2s ease; 
}

.container-footer a:hover {
  color: #18fc53; 
}